<template>
  <div class="card" :class="state.active" @click="popupOpen(num.index)">
    <span class="word">ㅏ</span>
    <ul class="popup-list">
      <li />
      <li />
    </ul>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  props: {
    state: {
      type: Object,
      default: null
    },
    num: {
      type: Object,
      default: null
    }
  },
  // mounted() {
  //   console.log('this.num', this.num)
  // },
  methods: {
    ...mapActions('cardView', ['popupOpen'])
  }
}
</script>

<style lang="scss" scoped>
$tab02Color: #c2acff;

.card {
  position: relative;
  width: 190px;
  height: 144px;
  margin: 8px;
  background: $colorF;
  border: 3px solid $tab02Color;
  border-radius: 20px;
  font-size: 84px;
  text-align: center;
  color: $color3;
  cursor: pointer;
  & > * {
    position: absolute;
  }
  .word {
    display: block;
    top: 13px;
    width: 100%;
    text-align: center;
  }
  .popup-list {
    right: 7px;
    bottom: 7px;
    font-size: 0;
    & > li {
      display: inline-block;
      width: 17px;
      height: 14px;
      background: $tab02Color;
      border-radius: 4px;
      & + li {
        margin-left: 4px;
      }
    }
  }
}
.disable {
  background: #f1f1f1;
  border: 3px solid $colorB;
  color: $colorB;
  cursor: default;
  .popup-list {
    display: none;
  }
}
</style>
