<template>
  <div class="oneday-wrap">
    <ul class="cont-list">
      <li class="step01">
        <router-link :to="{ name: 'onedayStep01' }">
          <h3 class="tit">기본 모음</h3>
        </router-link>
      </li>
      <li class="step02" :class="{ active: steps[0].clear }">
        <router-link :to="{ name: 'onedayStep02' }"
          ><h3 class="tit">기본 자음</h3></router-link
        >
      </li>
      <li class="step03" :class="{ active: steps[1].clear }">
        <router-link :to="{ name: 'onedayStep03' }"
          ><h3 class="tit">복잡한 모음</h3></router-link
        >
      </li>
      <li class="step04" :class="{ active: steps[2].clear }">
        <router-link :to="{ name: 'onedayStep04' }"
          ><h3 class="tit">대표 받침</h3></router-link
        >
      </li>
      <li class="step05" :class="{ active: steps[3].clear }">
        <router-link :to="{ name: 'onedayStep05' }"
          ><h3 class="tit">복잡한 받침</h3></router-link
        >
      </li>
      <li class="step06" :class="{ active: steps[4].clear }">
        <router-link :to="{ name: 'onedayStep06' }"
          ><h3 class="tit">겹받침</h3></router-link
        >
      </li>
    </ul>
    <div class="cont-play">
      <button class="btn-play" @click="complete">시작</button>
      <div v-if="!allClear" class="level">
        <h3 class="tit">나의 진도</h3>
        <div class="day">
          <span class="num">000</span>
          <span class="txt">일자</span>
        </div>
        <div class="step">
          <h4>복잡한 받침</h4>
          <span class="txt">ㅁㄴㄹㄷ</span>
        </div>
      </div>
      <div v-else class="complete">
        <h3 class="tit">한글 완료</h3>
      </div>
    </div>
  </div>
</template>

<script>
import { main } from '@/assets/js/data.js'
export default {
  data() {
    return {
      allClear: false,
      steps: main.steps
    }
  },
  mounted() {
    // console.log('main', main)
  },
  methods: {
    complete() {
      for (const s in this.steps) {
        this.steps[s].clear = true
      }
      this.allClear = true
    }
  }
}
</script>

<style lang="scss" scoped>
.oneday-wrap {
  height: 632px;
  padding: 42px 30px 30px;
  .cont-list {
    float: left;
    width: 850px;
    li {
      @include boxShadow(6px, 0.6);
      float: left;
      width: 270px;
      height: 270px;
      margin-left: 20px;
      background: $colorF;
      border-radius: 20px;
      font-family: 'tmoney-bold';
      font-size: 40px;
      text-align: center;
      color: $colorB;
      cursor: pointer;
      &:nth-child(n + 4) {
        margin-top: 20px;
      }
      &:nth-child(1),
      &:nth-child(4) {
        margin-left: 0;
      }
      a {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        .tit {
          position: absolute;
          top: 40px;
          display: block;
          width: 100%;
          font-size: 40px;
          text-align: center;
        }
        &::before {
          content: '';
          position: absolute;
          top: 116px;
          left: 85px;
          display: block;
          width: 110px;
          height: 114px;
          background: transparent
            url('~@/assets/images/main/oneday/menu_sprite.svg') no-repeat 5px 0;
        }
      }
      &.step01 {
        color: #ffb612;
      }
      &.step02 {
        a {
          &::before {
            background-position: -105px -124px;
          }
        }
        &.active {
          color: #fc615d;
          a {
            &::before {
              background-position: -105px 0;
            }
          }
        }
      }
      &.step03 {
        a {
          &::before {
            background-position: -215px -124px;
          }
        }
        &.active {
          color: #34cc67;
          a {
            &::before {
              background-position: -215px 0;
            }
          }
        }
      }
      &.step04 {
        a {
          &::before {
            background-position: -325px -124px;
          }
        }
        &.active {
          color: #00b4f2;
          a {
            &::before {
              background-position: -325px 0;
            }
          }
        }
      }
      &.step05 {
        a {
          &::before {
            background-position: -435px -124px;
          }
        }
        &.active {
          color: #ff5e9b;
          a {
            &::before {
              background-position: -435px 0;
            }
          }
        }
      }
      &.step06 {
        a {
          &::before {
            background-position: -545px -124px;
          }
        }
        &.active {
          color: #9a73ff;
          a {
            &::before {
              background-position: -545px 0;
            }
          }
        }
      }
    }
  }
  .cont-play {
    position: relative;
    float: right;
    width: 330px;
    height: 100%;
    font-family: 'tmoney-bold';
    .btn-play {
      position: absolute;
      top: 33px;
      right: -13.5px;
      width: 134px;
      height: 154px;
      background: url('~@/assets/images/main/oneday/btn_start.svg') no-repeat 0
        0;
      animation-name: btnAnimate;
      animation-duration: 1.4s;
      animation-duration: leaner;
      animation-iteration-count: infinite;
      font-size: 0;
      @keyframes btnAnimate {
        0% {
          transform: scale(1) rotate(-14deg);
        }
        50% {
          transform: scale(1.05) rotate(10deg);
        }
        100% {
          transform: scale(1) rotate(-14deg);
        }
      }
    }
    .level {
      @include boxShadow(6px, 0.6);
      position: relative;
      width: 100%;
      height: 334px;
      margin-top: 226px;
      border-radius: 20px;
      background: $colorF url('~@/assets/images/main/oneday/img_level_line.svg')
        no-repeat center 152px;
      .tit {
        position: absolute;
        top: -26px;
        left: -1px;
        width: 140px;
        height: 71px;
        background: url('~@/assets/images/main/oneday/txt_mylevel.svg')
          no-repeat 0 0;
        font-size: 26px;
        line-height: 44px;
        text-align: center;
        color: $colorF;
      }
      .day {
        position: relative;
        width: 100%;
        height: 154px;
        & > * {
          position: absolute;
        }
        .num {
          bottom: 30px;
          right: 96px;
          font-size: 70px;
          color: $color3;
        }
        .txt {
          top: 85px;
          right: 30px;
          font-size: 20px;
          color: $color9;
        }
      }
      .step {
        position: relative;
        width: 100%;
        height: 181px;
        & > * {
          position: absolute;
        }
        h4 {
          top: 30px;
          left: 80px;
          width: 170px;
          height: 47px;
          background: #ffb612;
          border-radius: 28px;
          text-align: center;
          line-height: 47px;
          color: $colorF;
        }
        .txt {
          top: 79px;
          width: 100%;
          font-size: 70px;
          text-align: center;
        }
      }
    }
    .complete {
      @include boxShadow(6px, 0.6);
      position: relative;
      width: 100%;
      height: 348px;
      margin-top: 212px;
      border-radius: 20px;
      background: #fff5ce url('~@/assets/images/main/oneday/img_complete.svg')
        no-repeat center 35px;
      .tit {
        position: absolute;
        top: -26px;
        left: 0;
        width: 140px;
        height: 71px;
        background: url('~@/assets/images/main/oneday/txt_complete.svg')
          no-repeat 0 0;
        font-size: 26px;
        line-height: 44px;
        text-align: center;
        color: $colorF;
      }
    }
  }
}
</style>
