<template>
  <div class="puzzle-wrap">
    <ol class="puzzle-list">
      <li v-for="(p, index) in 15" :key="index">
        <!-- start -->
        <div v-if="index === 0" class="puzzle start">
          <p class="txt">{{ mission[index].text }}</p>
          <div class="thume" />
          <button>
            <router-link :to="{ name: 'activity', params: { name: 'puzzle' } }">
