            </router-link>
          </button>
          <!-- <button>하루한글 시작</button> -->
        </div>
        <!-- play -->
        <div v-else-if="index === 1" class="puzzle play">
          <p class="txt">{{ mission[index].text }}</p>
          <div class="thume" />
          <button>{{ index }}차시 진행중</button>
        </div>
        <!-- clear -->
        <div v-else-if="index === 2" class="puzzle clear">
          <p class="txt">{{ mission[index].text }}</p>
          <div class="thume" />
          <button>{{ index }}차시 완료</button>
        </div>
        <!-- lock -->
