          position: absolute;
          top: -40px;
          left: 153px;
          width: 80px;
          height: 96px;
          background: no-repeat center 0;
        }
        & > * {
          position: absolute;
        }
        .txt {
          top: 56px;
          left: 0;
          width: 100%;
          text-align: center;
          color: $color6;
        }
        .thume {
          top: 120px;
          left: 96px;
          width: 200px;
          height: 200px;
        }
        button {
