          left: 40px;
          width: 306px;
          height: 70px;
          background: #00aaed;
          border-radius: 16px;
          font-family: 'tmoney-bold';
          font-size: 30px;
          color: $colorF;
          a {
            display: block;
            width: 100%;
            height: 100%;
            line-height: 70px;
          }
        }
        &.start {
          border: 3px solid #bbb;
          &::before {
            background-image: url('~@/assets/images/main/puzzle/ico_puzzle_lock.svg');
          }
        }
        &.play {
          $color: #34cc67;
          border: 3px solid $color;
