            background-image: url('~@/assets/images/main/puzzle/ico_puzzle_play.svg');
          }
          button {
            background: $color;
          }
        }
        &.clear {
          $color: #ff8000;
          border: 3px solid #ff8000;
          &::before {
            background-image: url('~@/assets/images/main/puzzle/ico_puzzle_clear.svg');
          }
          button {
            background: $color;
          }
        }
        &.lock {
          $color: #bbb;
          border: 3px solid $color;
